.calculator-section {
  width: 50%;
}

.calculator-section,
.calculate-button {
  color: var(--color-slate-900);
}

.input-title {
  color: var(--color-slate-700);
}

.radio-label,
.calculate-button {
  font-weight: var(--font-bold);
}

.clear-button,
.calculate-button {
  font-family: var(--font-family);
}

.calculator-header-container {
  display: flex;
  justify-content: space-between;
}

.clear-button {
  padding: 0;
  background: none;
  border: none;
  font-size: 1rem;
  text-decoration: underline;
  text-align: left;
  cursor: pointer;
  color: var(--color-slate-700);
}

.calculator-form {
  gap: 30px;
  display: flex;
  flex-direction: column;
  margin: 30px 0;
}

.mortgage-interest-container {
  display: flex;
  gap: 20px;

  > .input-container {
    width: 50%;
  }
}

.radio-container {
  display: flex;
  align-items: center;
  border: solid 1px var(--color-slate-500);
  border-radius: 4px;
  padding: 10px;
  margin-top: 10px;

  &:hover {
    border-color: var(--color-lime);
  }

  &.selected {
    border-color: var(--color-lime);
    background: hsl(61deg 70% 52% / 20%);
  }
}

.radio-label:hover {
  cursor: pointer;
}

.custom-radio {
  border: solid 1px var(--color-slate-500);
  appearance: none;
  -webkit-appearance: none;
  -moz-appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  margin-right: 15px;
}

.custom-radio:checked {
  background: var(--color-lime);
  border-radius: 50%;
  border: solid 3px var(--color-white);
  box-shadow: 0px 0px 1px 1px var(--color-lime);
}

.calculate-button {
  background: var(--color-lime);
  border: none;
  padding: 10px 30px;
  border-radius: 180px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
}

.calculator-icon {
  margin-right: 10px;
  height: 20px;
}

@media screen and (max-width: 375px) {
  .calculator-section {
    width: calc(100% - 40px); /* subtracted padding */
  }

  .calculator-header-container {
    flex-direction: column;
  }

  .clear-button {
    margin-top: 10px;
  }

  .calculator-form {
    gap: 20px;
    margin: 20px 0;
  }

  .mortgage-interest-container {
    flex-direction: column;

    > .input-container {
      width: 100%;
    }
  }

  .calculate-button {
    width: 100%;
  }
}
